import { useRef } from "react";
import { Form } from "usetheform";
import { v4 as uuidv4 } from "uuid";

import { Switch } from "./../Switch/Switch";
import { TextField } from "./../TextField/TextField";

import { useAppContext } from "./../../hooks/useAppContext";

export function CloudConnector() {
  const prevState = useRef({});
  const { socket } = useAppContext();

  const onChange = state => {
    if (prevState.current.cloudEnabled && !state.cloudEnabled) {
      socket.close();
    } else if (!prevState.current.cloudEnabled && state.cloudEnabled) {
      socket.connect(state.uuid);
    }
    prevState.current = state;
  };

  return (
    <div className="CloudConnector">
      <Form reducers={createRigID} onChange={onChange}>
        <Switch name="cloudEnabled" label="Enabling Cloud" />
        <div style={{ width: "300px" }}>
          <TextField name="uuid" inputProps={{ readOnly: true }} />
        </div>
      </Form>
      <button onClick={() => socket.sendCommand({ video: "video" })}>
        SendMessage Client LOCAL
      </button>
    </div>
  );
}

function createRigID(state) {
  const newState = { ...state };
  if (state.cloudEnabled) {
    const uid = uuidv4();
    newState.uuid = uid;
  } else {
    delete newState.uuid;
  }
  return newState;
}
